@charset "UTF-8";
.fpt-sub-banner {
  position: relative;
}
.fpt-sub-banner .slogan {
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translate(-50%, 50%);
}
@media (max-width: 576px) {
  .fpt-sub-banner .slogan {
    max-width: 12rem;
  }
  .fpt-sub-banner .slogan img {
    max-height: 1rem;
  }
}
.fpt-sub-banner .breadcrumb-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}
.fpt-sub-banner .breadcrumb-wrapper .container {
  background: rgba(242, 111, 33, 0.5);
}
.fpt-sub-banner .breadcrumb-wrapper ol {
  padding: 8px 0 8px 8px;
  list-style: none;
  display: flex;
}
.fpt-sub-banner .breadcrumb-wrapper ol li + li {
  margin-left: 16px;
  padding-left: 16px;
  position: relative;
}
.fpt-sub-banner .breadcrumb-wrapper ol li + li:before {
  position: absolute;
  left: -8px;
  top: 55%;
  transform: translateY(-50%);
  display: inline-block;
  font-family: "Material Design Icons";
  font-size: inherit;
  text-rendering: auto;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  content: "\f142";
  color: #fff;
  font-size: 18px;
}
.fpt-sub-banner .breadcrumb-wrapper ol li a {
  color: #fff;
  text-decoration: none;
  white-space: nowrap;
  font-family: "Muli";
  font-size: 15px;
}
@media (max-width: 768px) {
  .fpt-sub-banner .breadcrumb-wrapper {
    display: none;
  }
}

.FAQs .container {
  padding-top: 50px;
  padding-bottom: 70px;
  background: #ffffff;
}
.FAQs .tabs-wrapper {
  counter-reset: tabs-item;
}
.FAQs .tabs {
  transition: all 0.3s ease-in-out;
}
.FAQs .tabs a {
  text-decoration: none;
  background: #f8f8f8;
  color: #333;
  padding: 10px 30px;
  width: 100%;
  display: block;
}
@media (min-width: 992px) {
  .FAQs .tabs:hover {
    cursor: pointer;
    color: #fff;
  }
  .FAQs .tabs:hover a {
    background: #F26F21;
    color: #fff;
  }
}
.FAQs .tabs.active a {
  background: #F26F21;
  color: #fff;
}
.FAQs .tabs:not(:last-child) {
  border-bottom: 1px solid #EDEDED;
}
.FAQs .title {
  color: #F26F21;
  font-weight: 700;
  font-size: 24px;
  margin-bottom: 15px;
  border-bottom: 1px solid #E0E0E0;
}
.FAQs .tabs-title {
  display: flex;
  align-items: center;
  padding: 17px 20px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.3);
}
.FAQs .tabs-title span {
  font-size: 24px;
  color: #999999;
}
.FAQs .tabs-title span:before {
  counter-increment: tabs-item;
  content: counter(tabs-item, decimal-leading-zero);
}
.FAQs .tabs-title p {
  color: #333333;
  font-family: Muli, sans-serif;
  font-weight: 600;
  font-size: 16px;
  margin: 0 20px;
}
.FAQs .tabs-title i {
  font-size: 18px;
  margin-left: auto;
  transition: all 0.3s ease-in-out;
}
.FAQs .tabs-title.active i {
  transform: rotate(180deg);
}
.FAQs .tabs-item {
  transition: all 0.5s ease-in-out;
}
.FAQs .tabs-item:hover {
  cursor: pointer;
}
.FAQs .tabs-item p {
  font-family: Muli, sans-serif;
  font-size: 15px;
  color: #666666;
}
.FAQs .tabs-item.active .tabs-title span, .FAQs .tabs-item.active .tabs-title p, .FAQs .tabs-item.active .tabs-title i {
  color: #f26f21;
}
.FAQs .tabs-item.active .tabs-title span, .FAQs .tabs-item.active .tabs-title p {
  font-weight: 700;
}
.FAQs .tabs-item.active .tabs-title i:before {
  content: "\e873";
}
.FAQs .tabs-item.active .tabs-content {
  display: block;
}
.FAQs .tabs-content {
  transition: all 0.5s ease-in-out;
  display: none;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 30px;
}
.FAQs .tabs-content table {
  width: 100% !important;
}
.FAQs .tabs-wrapper {
  transition: all 0.5s ease-in-out;
}
.FAQs .btn-more {
  color: #f26f21;
  border-color: #f26f21;
}
.FAQs .btn-more:hover {
  background: #f26f21;
  color: #fff;
}
.FAQs .btn-more span:after {
  display: inline-block;
  font-family: "Material Design Icons";
  font-size: inherit;
  text-rendering: auto;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  content: "\f35d";
  margin-left: 4px;
}
@media (max-width: 992px) {
  .FAQs .title {
    font-size: 16px;
  }
  .FAQs .tabs:last-child {
    margin-bottom: 30px;
  }
  .FAQs .tabs-title {
    padding: 15px;
  }
  .FAQs .tabs-title span {
    font-size: 16px;
  }
  .FAQs .tabs-title p, .FAQs .tabs-title i {
    font-size: 13px;
  }
  .FAQs .tabs-title p {
    margin: 0 10px;
  }
}
@media (min-width: 992px) {
  .FAQs .tabs-title:hover {
    box-shadow: 0 0 5px 1px #DADADA;
  }
  .FAQs .tabs-title:hover span, .FAQs .tabs-title:hover p, .FAQs .tabs-title:hover i {
    color: #f26f21;
  }
  .FAQs .tabs-title:hover span, .FAQs .tabs-title:hover p {
    font-weight: 700;
  }
}

.newspager {
  display: none;
}